import React, { Component } from "react";
import Header from "../../components/header/Header";
import Footer from "../../components/footer/Footer";
import TopButton from "../../components/topButton/TopButton";
import ExperienceAccordion from "../../containers/experienceAccordion/ExperienceAccordion.js";
import "./Experience.css";
//import experience from "../../shared/opensource/experience_data.json"
import { Fade } from "react-reveal";

const experience = {
  "title": "Experience",
  "subtitle": "Work, Internship and Volunteership",
  "description":
  "I am currently working as an intern in IoT Laboratory at FPT Telecom. Besides, I love organising events and that is why I also involve in some non-profit organizations.",
  "header_image_path": "feelingProud.svg",
  "sections": [
    {
      "title": "Work",
      "experiences": [
      ]
    },
    {
      "title": "Internships",
      "experiences": [
        {
          "title": "IoT Laboratory Intern",
          "company": "FPT Telecom",
          "company_url": "https://fpt.vn/vi",
          "logo_path": "fpt.png",
          "duration": "May 2021 - present",
          "location": "Ho Chi Minh City, Vietnam",
          "description": "I am working on a project of event-driven programming in embedded systems.",
          "color": "#ee3c26"
        }
      ]
    },
    {
      "title": "Volunteerships",
      "experiences": [
        {
          "title": "Core Member",
          "company": "Ho Chi Minh Communist Youth Union of the Faculty of Electrical and Electronics Engineering",
          "company_url": "https://www.facebook.com/Trang-Th%C3%B4ng-Tin-Khoa-%C4%90i%E1%BB%87n-%C4%90i%E1%BB%87n-T%E1%BB%AD-277958596293035",
          "logo_path": "tncs.png",
          "duration": "Sep 2018 - present",
          "location": "Ho Chi Minh City, Vietnam",
          "description":
            "Orgainzing activities for students in the faculty.\n Some noticeable activities are Launch Time 2020 - Line Follower Robot Contest and Faculty Science and Technology Fair.",
        
          "color": "#4285F4"
          },
          {
          "title": "Volunteer",
          "company": "The Voluntary Spring Program 2020",
          "company_url": "https://www.facebook.com/Trang-Th%C3%B4ng-Tin-Khoa-%C4%90i%E1%BB%87n-%C4%90i%E1%BB%87n-T%E1%BB%AD-277958596293035",
          "logo_path": "xtn.png",
          "duration": "Dec 2019 - Feb 2020",
          "location": "Ho Chi Minh City, Vietnam",
          "description":
            "Orgainzing activities for children.",
          "color": "#4285F4"
          }
      ]
    }
  ]
}

class Experience extends Component {
  render() {
    const theme = this.props.theme;
    return (
      <div className="experience-main">
        <Header theme={theme} />
        <div className="basic-experience">
          <Fade bottom duration={2000} distance="40px">
            <div className="experience-heading-div">
              <div className="experience-heading-img-div">
                {<img
									src={require(`../../assests/images/${experience["header_image_path"]}`)}
									alt=""
								/>
              }
                
              
              </div>
              <div className="experience-heading-text-div">
                <h1
                  className="experience-heading-text"
                  style={{ color: theme.text }}
                >
                  {experience.title}
                </h1>
                <h3
                  className="experience-heading-sub-text"
                  style={{ color: theme.text }}
                >
                  {experience["subtitle"]}
                </h3>
                <p
                  className="experience-header-detail-text subTitle"
                  style={{ color: theme.secondaryText }}
                >
                  {experience["description"]}
                </p>
              </div>
            </div>
          </Fade>
        </div>
        <ExperienceAccordion sections={experience["sections"]} theme={theme} />
        <Footer theme={this.props.theme} onToggle={this.props.onToggle} />
        <TopButton theme={this.props.theme} />
      </div>
    );
  }
}

export default Experience;
